type MenuItem = {
  key: React.Key;
  icon?: React.ReactNode;
  children?: MenuItem[];
  label: React.ReactNode; 
  tag?: string;
};

function getItem(
  label: React.ReactNode,
  key: React.Key,
  icon?: React.ReactNode,
  children?: MenuItem[],
  tag?: string,
): MenuItem {
  return {
    key,
    icon,
    children,
    label,
    tag,
  };
}


